import React, { Component } from 'react';
import Slider from 'react-slick';
import TestimonialsItems from './TestimonialsItems/TestimonialsItems';
import classes from './TestimonialsSection.css';

class TestimonialsSection extends Component {
    render(){
      let settings = {
          dots: true,
          infinite: true,
          speed: 500,
          slidesToShow: 1,
          slidesToScroll: 1,
          arrows: false,
          autoplay: true,
          autoplaySpeed: 5000
        };
      let count = Math.floor(this.props.item.testimonials.length/this.props.testimonialsPerPage).toFixed(0);
      this.props.windowDimensions.width<1024 && (count=this.props.item.testimonials.length-1);
      let testimItems=[];
      while (count){
        testimItems[count-=1] = <div
                                  key={count}>
                                    <TestimonialsItems
                                      currentIndex={count}
                                      item={this.props.item}
                                      testimonialsPerPage={this.props.testimonialsPerPage}
                                      windowDimensions={this.props.windowDimensions}/>
                                </div>
      }
        return (
          <Slider
            className={classes.TestimonialsSection}
            {...settings}>
            {testimItems}
          </Slider>
        );
    }
}

export default TestimonialsSection;



