import React from 'react';
import TestimonialsItem from './TestimonialsItem/TestimonialsItem'
import classes from './TestimonialsItems.css';

const testimonialsItems = (props) => {

      return (
        <div className={classes.TestimonialsItems}>
          <TestimonialsItem
              testimonialsPerPage={props.testimonialsPerPage}
              item={props.item}
              windowDimensions={props.windowDimensions}
              currentIndex={props.currentIndex}/>
        </div>
      )
}

export default testimonialsItems;



