import React from 'react';
import classes from './BrandsSection.css';

const brandsSection = (props) => {
    let brandLogoClass = props.windowDimensions.width<768 ? classes.BrandLogoMobile : classes.BrandLogo,
        brandLogoActive = props.item.active ? classes.BrandLogoActive : null,
        counter = 0;
    const brands = props.item.products.map((product, index) => {
        let logos = product.brands.map((logo, i) => {
            props.item.active && (counter+=0.07);
            return <img
                      className={brandLogoClass + ' ' + brandLogoActive}
                      src={logo.img}
                      alt={logo.brandName}
                      key={i}
                      style={{transitionDelay:counter+'s'}}
                    />
        })
        return  <div key={index} className={classes.ProductsContainer}>
                  <div className={classes.ProductsName}>{product.name}</div>
                  {logos}
                </div>
    })
    return (
      <div className={classes.BrandsWrp}>
        <h2 className={classes.BrandSentence}>{props.item.sentence}</h2>
        {brands}
      </div>
    )
}

export default brandsSection;



