import React from 'react';
import classes from './Logo.css';

const logo = (props) => (
  <div
    className={classes.Logo}
    style={{color:props.navItems[props.currentSectionIndex].textColor}}
    onClick={() => props.toggle(0)}>
    VTService
  </div>
)

export default logo;



