import React from 'react';
//import Adj from '../../../../../../hoc/AdjComponent/AdjComponent';
import IconStar from '../../../../../IconStar/IconStar';
import classes from './TestimonialsItem.css';

const testimonialsItem = (props) => {
    let starsExtractor = (stars) => {
      let starsElements = [];
      while (stars>0) {
        stars--;
        starsElements.push(<span key={stars}><IconStar index={stars}/></span>);
      }
      return starsElements;
    }
    let testimonial;
    let attachedClasses=[classes.TestimonialContainer];
    let endPoint = (props.currentIndex+1)*props.testimonialsPerPage;
    let startPoint = endPoint-props.testimonialsPerPage;
    let testimonials = props.item.testimonials.slice(startPoint, endPoint);
    
    if (props.item.active || props.windowDimensions.width<1024){
      attachedClasses.push(classes.TestimonialContainerActive);
    }else {
      attachedClasses=[classes.TestimonialContainer];
    }
    if (props.windowDimensions.width>=1024){
      testimonial = testimonials.map((testimonial, index, testimonials) => {
          return <div
                    key={index}
                    className={attachedClasses.join(' ')}
                    style={{width:100/props.testimonialsPerPage+'%',
                            transitionDelay:index/6+'s'}}>
                    <div className={classes.InnerContainer}>
                      <div className={classes.Author}>{testimonial.author}</div>
                      <div className={classes.TestimDate}>{testimonial.date}</div>
                      <div className={classes.Stars}>{starsExtractor(testimonial.stars)}</div>
                      <div className={classes.Review}>{testimonial.review}</div>
                    </div>
                  </div>
        })
    }
    if (props.windowDimensions.width<1024) {
      testimonial = <div
                      className={attachedClasses.join(' ')}>
                        <div className={classes.Author}>{props.item.testimonials[props.currentIndex].author}</div>
                        <div className={classes.TestimDate}>{props.item.testimonials[props.currentIndex].date}</div>
                        <div className={classes.Stars}>{starsExtractor(props.item.testimonials[props.currentIndex].stars)}</div>
                        <div className={classes.Review}>{props.item.testimonials[props.currentIndex].review}</div>
                      </div>
    }


    return(
      <div className={classes.TestimonialWrp}>
         {testimonial}
      </div>
    )
}

export default testimonialsItem;



