import React from 'react';
import classes from './NavigationItem.css';

const navigationItem = (props) => {
    let attachedClasses = [classes.NavigationItem];
    if (props.active && props.windowDimensions.width > 767){
      attachedClasses.push(classes.NavigationItemActive);
    } else {
      attachedClasses = [classes.NavigationItem];
    }
          return (<div
                    onClick={() => props.toggle(props.navIndex)}
                    className={attachedClasses.join(' ')}>
                    {props.itemTitle}
                  </div>)
}

export default navigationItem;



